* {
  box-sizing: border-box;
}

.bodyexpenses {
  background-color: #f7f7f7;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  margin: 0;
  font-family: "Lato", sans-serif;
}

.expensescontainer {
  margin: 30px auto;
  width: 350px;
}

.h1expenses {
  letter-spacing: 1px;
  margin: 0;
}

.h3expenses {
  border-bottom: 1px solid #bbb;
  padding-bottom: 10px;
  margin: 40px 0 10px;
}

.h4expenses {
  margin: 0;
  text-transform: uppercase;
}

.inc_exp_container {
  background-color: #fff;
  box-shadow: var(--box-shadow);
  padding: 20px;
  display: flex;
  justify-content: space-between;
  margin: 20px 0;
}

.inc_exp_container > div {
  flex: 1;
  text-align: center;
}

.inc_exp_container > div:first-of-type {
  border-right: 1px solid #dedede;
}

.moneyexp {
  font-size: 20px;
  letter-spacing: 1px;
  margin: 5px 0;
}

.moneyexp.plus {
  color: #2ecc71;
}

.moneyexp.minus {
  color: #c0392b;
}

.labelexpenses {
  display: inline-block;
  margin: 10px 0;
}

.inputexpenses[type="text"],
.inputexpenses[type="number"] {
  border: 1px solid #dedede;
  border-radius: 2px;
  display: block;
  font-size: 16px;
  padding: 10px;
  width: 100%;
  color: #7400b8;
}

.btnexpenses {
  cursor: pointer;
  background-color: #9c88ff;
  box-shadow: var(--box-shadow);
  color: #fff;
  border: 0;
  display: block;
  font-size: 16px;
  margin: 10px 0 30px;
  padding: 10px;
  width: 100%;
}

.btnexpenses:focus,
.deletebtnexpenses:focus {
  outline: 0;
}

.listexpenses {
  list-style-type: none;
  padding: 0;
  margin-bottom: 40px;
}

.listexpenses li {
  background-color: #fff;
  box-shadow: var(--box-shadow);
  color: #333;
  display: flex;
  justify-content: space-between;
  position: relative;
  padding: 10px;
  margin: 10px 0;
}

.listexpenses li.plus {
  border-right: 5px solid #2ecc71;
}

.listexpenses li.minus {
  border-right: 5px solid #c0392b;
}

.deletebtnexpenses {
  cursor: pointer;
  background-color: #e74c3c;
  border: 0;
  color: #fff;
  font-size: 20px;
  line-height: 20px;
  padding: 2px 5px;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translate(-100%, -50%);
  opacity: 0;
  transition: opacity 0.3s ease;
}

.listexpenses li:hover .deletebtnexpenses {
  opacity: 1;
}
