/* tables CSS*/
div {
  box-sizing: border-box;
}

.table-container-6 {
  font-size: 20px;
  display: block;
  margin: 2em auto;
  width: 90%;
  max-width: 1200px;
  border-radius: 5px 5px 0 0;
  overflow: hidden;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.15);
}

.flex-table {
  display: flex;
  flex-flow: row wrap;
  transition: all 0.25s cubic-bezier(0.215, 0.61, 0.355, 1);
  border-radius: 5px 5px 5px 5px;
}
/* .flex-table:first-of-type { add  borders if u wish later
  border-top: solid 1px #000000;
  border-left: solid 1px #000000;
} */
.flex-table:first-of-type .flex-row-6 {
  background: rgb(66, 65, 65);
  color: white;
  border-color: rgb(45, 45, 45);
}
.flex-table.row:nth-child(odd) .flex-row-6 {
  background: #f4f2f1;
}
.flex-table:hover {
  /* transition: all 0.25s cubic-bezier(0.215, 0.61, 0.355, 1); */
  box-shadow: 30px 0 30px #c7c7c7;
  transform: scale(1.01, 1.05);
}

.flex-row-6 {
  width: calc(100% / 6);
  text-align: center;
  padding: 0.5em 0.5em;
  border-right: solid 1px #d9d9d9;
  border-bottom: solid 1px #d9d9d9;
}

.rowspan {
  display: flex;
  flex-flow: row wrap;
  align-items: flex-start;
  justify-content: center;
}

.column {
  display: flex;
  flex-flow: column wrap;
  width: 75%;
  padding: 0;
}
.column .flex-row-6 {
  display: flex;
  flex-flow: row wrap;
  width: 100%;
  padding: 0;
  border: 0;
  border-bottom: solid 1px #d9d9d9;
}
.column .flex-row-6:hover {
  background: #f5f5f5;
}
/*flex-cell used only nested cell structure*/
.flex-cell {
  width: calc(100% / 5);
  text-align: center;
  padding: 0.5em 0.5em;
  border-right: solid 1px #d9d9d9;
}

/*Table css ends */

