code[class*="language-"],
pre[class*="language-"] {
  color: #ccc;
  background: none;
  font-family: Consolas, Monaco, "Andale Mono", "Ubuntu Mono", monospace;
  font-size: 1em;
  text-align: left;
  white-space: pre;
  word-spacing: normal;
  word-break: normal;
  word-wrap: normal;
  line-height: 1.5;

  -moz-tab-size: 4;
  -o-tab-size: 4;
  tab-size: 4;

  -webkit-hyphens: none;
  -moz-hyphens: none;
  -ms-hyphens: none;
  hyphens: none;
}

/* Code blocks */
pre[class*="language-"] {
  padding: 1em;
  margin: 0.5em 0;
  overflow: auto;
}

:not(pre) > code[class*="language-"],
pre[class*="language-"] {
  background: #2d2d2d;
}

/* Inline code */
:not(pre) > code[class*="language-"] {
  padding: 0.1em;
  border-radius: 0.3em;
  white-space: normal;
}

.token.comment,
.token.block-comment,
.token.prolog,
.token.doctype,
.token.cdata {
  color: #999;
}

.token.punctuation {
  color: #ccc;
}

.token.tag,
.token.attr-name,
.token.namespace,
.token.deleted {
  color: #e2777a;
}

.token.function-name {
  color: #6196cc;
}

.token.boolean,
.token.number,
.token.function {
  color: #7dbee9;
}

.token.property,
.token.class-name,
.token.constant,
.token.symbol {
  color: #f8c555;
}

.token.selector,
.token.important,
.token.atrule,
.token.keyword,
.token.builtin {
  color: #cc99cd;
}

.token.string,
.token.char,
.token.attr-value,
.token.regex,
.token.variable {
  color: #7ec699;
}

.token.operator,
.token.entity,
.token.url {
  color: #67cdcc;
}

.token.important,
.token.bold {
  font-weight: bold;
}
.token.italic {
  font-style: italic;
}

.token.entity {
  cursor: help;
}

.token.inserted {
  color: green;
}

.command-line-prompt {
  border-right: 1px solid #999;
  display: block;
  float: left;
  font-size: 100%;
  letter-spacing: -1px;
  margin-right: 1em;
  pointer-events: none;

  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.command-line-prompt > span:before {
  color: #999;
  content: " ";
  display: block;
  padding-right: 0.8em;
}

.command-line-prompt > span[data-user]:before {
  content: "[" attr(data-user) "@" attr(data-host) "] $";
}

.command-line-prompt > span[data-user="root"]:before {
  content: "[" attr(data-user) "@" attr(data-host) "] #";
}

.command-line-prompt > span[data-prompt]:before {
  content: attr(data-prompt);
}

/*css added by me-------------------------------------------------------------*/

/*code container css*/
.gatsby-highlight {
  transition: all 0.25s cubic-bezier(0.215, 0.61, 0.355, 1);
}

.gatsby-highlight:hover {
  border-radius: 30px 20px 100px 20px;
  box-shadow: 0 0 50px #515151;
  transform: scale(1.01, 1.05);
}

.gatsby-highlight > pre {
  color: rgb(255, 255, 255);
  tab-size: 1.5em;
  background: rgb(40, 44, 52);
  border-radius: 30px 20px 100px 20px;
  overflow: auto;
}
.gatsby-highlight > pre > code {
  color: rgb(255, 255, 255);
}

/*right side container css*/
.right-div-highlight {
  transition: all 0.25s cubic-bezier(0.215, 0.61, 0.355, 1);
  border-radius: 20px 30px 20px 100px;
  box-shadow: 0 0 50px #515151;
}

.right-div-highlight:hover {
  transform: scale(1.01, 1.05);
}

/* WINDOWS DARK MODE MEDIA QUERY */
@media (prefers-color-scheme: dark) {
  .right-div-highlight,
  .gatsby-highlight:hover {
    box-shadow: 0 0 50px #cecece;
  }
}
/* WINDOWS DARK MODE MEDIA QUERY ENDS */
